





















	import app from '../../App.vue';
	var sourceType = [
		['camera'],
		['album'],
		['camera', 'album']
	];

	export default {
		data() {
			return {
				imageSrc:'',
				nickname:'',
				fansCounts:'',
				followCounts:'',
				receiveLikeCounts:'',
				sourceTypeIndex: 2,
				sourceType: ['拍摄', '相册', '拍摄或相册'],
				videoSrc: ''
			};
		},
		created() {
			var userInfo = app.getGlobalUserInfo();
			if(userInfo != null && userInfo != undefined){
				this.imageSrc = app.serverUrl + userInfo.faceImage;
				this.nickname = userInfo.nickname;
				this.fansCounts = userInfo.fansCounts;
				this.followCounts = userInfo.followCounts;
				this.receiveLikeCounts = userInfo.receiveLikeCounts;
			}
			
			
			
		},
		methods:{
			chooseImage: function () {
							uni.showLoading({});
							uni.chooseImage({
								count: 1,
								sizeType: ['compressed'],
								sourceType: ['album'],
								success: (res) => {
									console.log('chooseImage success, temp path is', res.tempFilePaths[0])
									var imageSrc = res.tempFilePaths[0];
									var user = app.getGlobalUserInfo();
									uni.uploadFile({
										url: app.serverUrl + "/user/upload?userId=" + user.id,
										filePath: imageSrc,
										name: 'file',
										success: (res) => {
											console.log('uploadImage success, res is:', res)
											uni.showToast({
												title: '上传成功',
												icon: 'success',
												duration: 1000
											});
											var resObject = JSON.parse(res.data);
											var userInfo = resObject.data;
											console.log(userInfo);
											var faceImage = app.serverUrl + userInfo.faceImage;
											
											this.imageSrc = faceImage;
											console.log(this.imageSrc);
										},
										fail: (err) => {
											console.log('uploadImage fail', err);
											uni.showModal({
												content:err.errMsg,
												showCancel:false
											});
											uni.hideLoading();
										},
										complete: () => {
											console.log("complate")
										}
									})
			
								},
								fail: (err) => {
									console.log('chooseImage fail', err)
									uni.hideLoading();
								}
							})
						},
						chooseVideo:function(){
								uni.navigateTo({
									url:'../uploadVideo/uploadVideo'
								})
// 								uni.chooseVideo({
// 									sourceType: ['album'],
// 									success: (res) => {
// 										this.videoSrc = res.tempFilePath
// 										console.log(res.duration);
// 									}
// 								})
// 							
						},
						
		}
	}



